import React, { useState, useEffect } from 'react';

function Albums() {
  const [loading, setLoading] = useState(false);
  const [albums, setAlbums] = useState([]);
  const [error, setError] = useState(null);

  useEffect(() => {
    getAlbums()  
  }, [])

  const getAlbums = () => {
    setLoading(true);
    setAlbums([]);
    setError(null);
    fetch('https://jsonplaceholder.typicode.com/albums')
      .then(response => response.json())
      .then(data => {
        console.log("GET albums", data);
        setLoading(false);
        setAlbums(data);
        setError(null);
      })
      .catch(err => {
        setLoading(false);
        setAlbums([]);
        setError(err);
      })
  }

  if (loading) {
    return <p>Loading ...</p>
  } else if (error) {
    return <p>Error !!</p>
  } else {
    return (
      <div className='tc'>
        <h1>Albums</h1>
        
        { albums.map(album => <p key={album.id}>{album.title}</p>) }
      </div>
    )
  } 
}

export default Albums