import React, { useState } from 'react';
import { createPortal } from 'react-dom';

function Geolocation() {
  const [location, setLocation] = useState({
    longitude: 0,
    latitude: 0
  });

  const showPosition = (position) => {
    console.log('show position', position);
    setLocation({
      longitude: position.coords.longitude,
      latitude: position.coords.latitude
    })
  }

  const manageError = (err) => {
    console.log('error get geolocation', err);
  }

  const getGeolocation = () => {
    if(navigator.geolocation) {
      navigator.permissions.query({name:'geolocation'}).then(function(result) {
        console.log('result', result);
        if (result === 'granted') {
          navigator.geolocation.getCurrentPosition(showPosition, manageError);
        }
      });
    } else {
      console.log("Geolocation is not supported by your browser");
    }
  }

  return (
      <div className='tc'>
        <button onClick={getGeolocation}>Geolocation</button>
        <p>Latitude: {location.latitude}</p>
        <p>Longitude: {location.longitude}</p>
      </div>
  )
}

export default Geolocation